},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row align-items-center justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-3"},[_vm._v("La agroecología cumple un papel central, referido en la Agenda 2030 de la ONU, en la construcción de ciudades y comunidades sostenibles, la reducción de las desigualdades, la igualdad de género, la acción por el clima, entre otros. Específicamente, se relaciona con el Objetivo de Desarrollo Sostenible (ODS) N.° 2: Hambre cero, teniendo en cuenta que la agricultura es la principal fuente de ingresos, empleos y garantiza la seguridad alimentaria en los países en desarrollo. Por otro lado, el ODS N.° 12: Consumo y producción sostenible, partiendo del hecho de que el 30 % del consumo de energía en el mundo y el 22 % del total de emisiones de gases de efecto invernadero provienen del sector de la alimentación. De este modo, se plantea la necesidad de detener la degradación de los suelos, el uso insostenible del agua y la pérdida de biodiversidad agrícola de cara a sistemas de producción más resilientes y sostenibles (FAO, 2018). ")])]),_c('div',{staticClass:"col-7 col-lg-4",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_23.png"),"alt":""}})])])
