},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row align-items-center justify-content-center mb-5"},[_c('div',{staticClass:"col-7 col-lg-4",attrs:{"data-aos":"fade-right"}},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_24.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-3"},[_vm._v("El concepto que abarca esta definición, integra las interacciones e interconexiones entre componentes sociales y ecológicos. Una estrategia clave para lograr una agricultura sostenible es restituir la diversidad a los países agropecuarios, dado que una de las principales problemáticas de la agricultura moderna, tiene relación con la pérdida de la biodiversidad, causada, principalmente, por el aumento de los monocultivos, derivando en una uniformidad genética de los países y del suelo.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Por esta razón, es clave aumentar, restituir y mantener la biodiversidad de los agroecosistemas, pues tiene relación directa con los bienes y servicios ecosistémicos, tales como, el control de microclimas, el reciclaje de nutrientes, la regulación de plagas y procesos hidrológicos y el control de sustancias químicas, entre otros. La pérdida de estos servicios naturales, producto de la simplificación biológica, podría generar grandes impactos ambientales y económicos, dado que los agroecosistemas carecen de la capacidad de regular la fertilidad del suelo, el control de los nutrientes y el control natural de plagas. ")])])])
